@import url("https://fonts.googleapis.com/css?family=Noto+Sans+KR:300,400,500,700&display=swap&subset=korean");

/* reset */
body {
  font: normal 15px "Noto Sans KR", sans-serif;
  font-weight: 400;
  line-height: normal;
  color: #000;
  background-color: #fff;
  margin: 0;
  padding: 0;
  word-break: keep-all;
}
* {
  box-sizing: border-box;
}
b,
strong {
  font-weight: 700;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  font-size: 1em;
}
h1,
h2,
h3,
h4,
h5,
h6,
div,
p,
dl,
dt,
dd,
ul,
ol,
li,
form,
fieldset,
blockquote,
address,
table,
thead,
tbody,
tfoot,
tr,
td,
caption {
  margin: 0;
  padding: 0;
}
table {
  width: 100%;
  border-collapse: collapse;
}
ul,
ol {
  list-style: none;
}
a {
  color: #000;
  text-decoration: none;
}
img {
  vertical-align: top;
}
button {
  outline: 0;
}

/* Header */
.header {
  padding: 20px 0 30px;
  text-align: center;
}
.header h1 {
  margin-bottom: 40px;
}
.header p {
  font-weight: 300;
}
.year_list {
  display: flex;
  justify-content: center;
  font-weight: 500;
  letter-spacing: -0.5px;
}
.year_list > li {
  padding: 10px;
}
.year_list .active {
  color: #fff;
  background-color: #000;
  transition: 0.6s;
}

/* Contents */
.movie_list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0 50px;
}
.movie_list > li {
  width: 300px;
  margin: 0 15px 50px;
  border-bottom: 1px solid #555;
  text-align: center;
}
.movie_list > li a {
  position: relative;
  display: block;
}
.movie_list > li a .img_area {
  height: 428px;
  overflow: hidden;
}
.movie_list > li a .img_area img {
  width: 100%;
}
.movie_list > li a p {
  padding: 5px 0;
}
.movie_list > li a .desc_area {
  position: absolute;
  top: 0;
  bottom: 32px;
  left: 0;
  right: 0;
  opacity: 0;
  padding: 20px;
  background-color: #eee;
  text-align: left;
  transition: opacity 0.6s ease-in-out;
}
.movie_list > li a:hover .desc_area {
  opacity: 100;
}
.movie_list > li a .desc_area .title {
  margin-right: 8px;
  font-weight: bold;
}
